*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
.main{
    width: 100%;
    height: 100vh;
    background: rgb(228, 131, 184);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.mid{
    width: 25%;
    height: 80vh;
    background-color: rgb(255, 245, 249);
    box-shadow: 5px 5px 25px 5px rgb(61, 4, 68);
    border-radius: 5px;
}
h1{
    text-align: center;
    color: black;
    background-color: rgba(177, 6, 211, 0.623);
    padding: 8px 0 8px 0;
    margin-bottom: 15px;
}
input{
    text-align: center;
    height: 30px;
    top: 10px;
    width: 70%;
    border: 2px solid navy;
    outline: 5px;
    background: transparent;
    font-size: 20px;
    font-weight: 500;
    border-radius: 5px;
    margin-left: 25px;
    padding: 15px;
    margin-bottom: 15px;
    margin-top: 15px;
}
button{
    min-height: 35px;
    width: 35px;
    border: 2px solid black;
    border-radius: 50%;
    color: white;
    font-size: 20px;
    margin-left: 19px;
    margin-top: 10px;
    background-color: rgba(177, 6, 211, 0.623);
}
button:hover{
    background-color: green;
}
ul li{
    font-size: 25px;
    padding: 10px;
    
    width: 70%;
    margin-left: 30px;
    margin-top: 50px;
    height: 100px;
    align-items: center;
    display: inline;
    list-style-type: none;
    
}
#icon{
    margin-top: 20px;
    min-height: 30px;
    width: 30px;
    padding: 5px;
    border: 2px solid black;
    border-radius: 60%;
    color: red;
    font-size: 20px;
    margin-left: 15px;
    display: inline;
    justify-content: center;
    align-items: center;
    background-color: white;  
}
.todo{
    padding-top: 20px;
}